:root {
    --backgroundColorDefault: #dddddd;
    --backgroundColorHighlight: #f35429;
    --inputColorDefault: #111;
    --inputColorHighlight: #fff;
}
html {
    height: 100%;
  }
  
body {
  background-color: #01031c;
  color: white;
}

.content {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Verdana, sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

h1 {
  font-family: sans-serif;
}

.node text {
  font-size: 8px;
  pointer-events: none;
}
.node .node-text {
  fill: var(--inputColorHighlight);
  stroke: var(--inputColorHighlight);
  stroke-width: 0px;
  font-size: 14px;
  font-weight: bold;
  font-style: normal;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

path.hull {
  fill: lightsteelblue;
  fill-opacity: 0.3;
}

.config-box {
  font-family: sans-serif;
  padding-left: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-content: center;
          align-content: center;
}
.config-box div {
  display: inline;
}

#diagram {
  position: relative;
  margin: 0;
  padding: 0;
  background-color: #090c26;
  overflow: hidden;
}

.inverted#diagram {
  background-color: var(--backgroundColorDefault);
}
.inverted .node-text {
  fill: var(--inputColorDefault);
  stroke: var(--inputColorDefault);
}

#info-box {
  width: 90%;
  height: 100%;
  padding: 10px;
  display: inline-block;
  vertical-align: middle;
  font-family: "Lucida Console", Sans-serif;
}
#info-box .notes {
  font-size: 16px;
  margin-bottom: 8px;
}
#info-box table {
  font-size: 18px;
}
#info-box table tr :first-child {
  color: #999;
}
#info-box .timestamp {
  font-size: 8px;
  color: #999;
  font-style: italic;
  margin-top: 5px;
}

.loading-info, .debug-info {
  position: absolute;
  top: 0;
}

@media (max-width: 80em) {
  #diagram {
    -webkit-flex: 0 0 99%;
            flex: 0 0 99%;
    max-width: 99%;
  }

  #info-box {
    -webkit-flex: 0 0 98%;
            flex: 0 0 98%;
    max-width: 98%;
  }
}
@media (min-width: 80em) {
  body {
    overflow-y: hidden;
  }

  #diagram {
    -webkit-flex: 0 0 78%;
            flex: 0 0 78%;
    max-width: 78%;
  }

  #info-box {
    -webkit-flex: 0 0 19%;
            flex: 0 0 19%;
    max-width: 19%;
  }
}
@media (max-height: 800px) {
  #diagram {
    height: 560px;
  }

  #info-box {
    min-height: 10px;
  }
}
@media (min-height: 800px) and (max-height: 900px) {
  #diagram {
    height: 760px;
  }

  #info-box {
    height: 760px;
  }
}
@media (min-height: 900px) {
  #diagram {
    height: 850px;
  }

  #info-box {
    height: 850px;
  }
}
.row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.col-4, .col-6, .col-8 {
  position: relative;
  width: 100%;
  min-height: 1px;
}

.col-4 {
  -webkit-flex: 0 0 33%;
          flex: 0 0 33%;
  max-width: 33%;
}

.col-6 {
  -webkit-flex: 0 0 49%;
          flex: 0 0 49%;
  max-width: 49%;
}

.col-8 {
  -webkit-flex: 0 0 66%;
          flex: 0 0 66%;
  max-width: 66%;
}
input {
    border-style: none;
    line-height: 22px;
    margin-left: 5px;
    padding-left: 5px;
    border-radius: 1px;
  }
  input:focus {
    outline-color: var(--backgroundColorHighlight);
  }
  
  button {
    border-style: none;
    line-height: 22px;
    margin: 5px;
    border-radius: 1px;
    background-color: var(--backgroundColorDefault);
    color: var(--inputColorDefault);
  }
  button:focus {
    outline-color: var(--backgroundColorHighlight);
  }
  button:hover {
    background-color: var(--backgroundColorHighlight);
    color: var(--inputColorHighlight);
  }
  button:active {
    background-color: #ac2c0a;
    color: var(--inputColorHighlight);
  }
  button:disabled {
    background-color: #aaaaaa;
    color: var(--inputColorDefault);
  }
  
  input:disabled {
    background-color: #aaaaaa;
  }
  
  .disabled {
    color: #aaaaaa;
  }
  
  input[type=checkbox] {
    position: absolute;
    opacity: 0;
  }
  input[type=checkbox] + label {
    position: relative;
    cursor: pointer;
    margin-left: 20px;
  }
  input[type=checkbox] + label:before {
    content: "";
    margin-right: 5px;
    display: inline-block;
    vertical-align: text-top;
    width: 20px;
    height: 20px;
    background: var(--inputColorHighlight);
  }
  input[type=checkbox]:hover + label:before {
    background: var(--backgroundColorHighlight);
  }
  input[type=checkbox]:focus + label:before {
    box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12);
  }
  input[type=checkbox]:checked + label:before {
    background: var(--backgroundColorHighlight);
  }
  input[type=checkbox]:disabled + label {
    color: #aaaaaa;
    cursor: auto;
  }
  input[type=checkbox]:disabled + label:before {
    box-shadow: none;
    background: #aaaaaa;
  }
  input[type=checkbox]:checked + label:after {
    content: "";
    position: absolute;
    left: 5px;
    top: 9px;
    background: var(--inputColorHighlight);
    width: 2px;
    height: 2px;
    box-shadow: 2px 0 0 var(--inputColorHighlight), 4px 0 0 var(--inputColorHighlight), 4px -2px 0 var(--inputColorHighlight), 4px -4px 0 var(--inputColorHighlight), 4px -6px 0 var(--inputColorHighlight), 4px -8px 0 var(--inputColorHighlight);
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
  }
